    所以，顺其自然背后是什么在支撑着？这就是因果关系。所有的症状产生并非空穴来风，症状的存在并非坏事。所有心理症状的表现都是一种防御，也就是对你心理系统的保护。就好像感冒的出现是对你身体的保护，它是在提醒你，应该去杀灭病毒了。如果没有感冒这种症状的产生，那么当你身体已经完全不行了才提醒你，估计你就面临着死亡了。所以感冒这种症状的产生就是为了对你进行保护。
