    接下来我们来说说“为所当为”。第一个“为”是动词，“做”的意思；“所”这边可以理解为代词“我”；而“当”是应当，应该的意思；第二个“为”是代词，指代“事情”各种各样的事情。对于我们社恐的人来说，打招呼，社交，吃饭，等等都是“事情”。
