    那么现在问题来了，我该怎么去判断，这个事情是我应该做的事情？这个其实很简单，问问你自己，这件事情，是不是应该做？不做是否会让你变得为难？或者不符合你当下的想法呢？我们需要排除恐惧感去判断，不以恐惧感为判断标准，只以你当下的需求以及愿望为基础。
